$greyLight: rgb(209, 209, 209);
$greyDark: rgb(168, 168, 168);
$red: rgb(236, 75, 0);
$TextShadow: 0px 0px 1px rgb(255, 255, 255);
$fontSize50: 50px;
$fontSize24: 24px;
// $FontFamily: 'Goldman', cursive;
.results {
    position: relative;
    margin: 0 auto;
    margin-bottom: 20px;
    max-width: 800px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: $greyLight;
    z-index: 2;
    overflow: hidden;
    height: 0;
    transition: all 0.3s ease-in-out;
    letter-spacing: 1px;
    font-size: $fontSize24;
}

.active {
    height: 600px;
}

.table {
    padding: 20px;
    max-height: 300px;
    overflow: auto;
}

.tableTh {
    background-color: $red;
    color: black;
}

.tableTr {
    text-align: center;
    background-color: $greyDark;
}

.heading {
    font-size: $fontSize50;
    margin: 0;
    padding-top: 50px;
}